<template>
  <div class="number-manager">
    <span
      :class="{'number-manager-disabled':number===1}"
      class="number-manager-less"
      @click="onLessClick"
    >-</span>
    <span class="number-manager-number">{{defaultNumber}}</span>
    <span @click="onAddClick" class="num-manager-add">+</span>
  </div>
</template>
<script>
export default {
  props: {
    //   父组件指定的默认数量
    defaultNumber: {
      type: Number,
      default: 1
    }
  },
  data() {
    return {
      number: 1
    }
  },
  watch: {
    defaultNumber(newV) {
      this.number = newV
    },
    number(newV) {
      this.$emit('onChangeNumber', newV)
    }
  },
  methods: {
    //   减
    onLessClick() {
      if (this.number === 1) {
        return
      }
      this.number -= 1
    },
    //   加
    onAddClick() { this.number++ }
  }
}
</script>
<style lang="less" scoped>
.number-manager {
  display: flex;
  height: 2rem;
  line-height: 2rem;
  span {
    display: inline-block;
    width: 2.2rem;
    text-align: center;
    font-weight: 500;
    font-size: 1.6rem;
  }
  .number-manager-number {
    background-color: #eee;
    padding: 0 0.6rem;
    font-size: 1rem;
  }
}
</style>
