<template>
  <div class="shopping">
    <!-- 头部 -->
    <navgation-bar :isShowBack="false" :pageName="'购物车'"></navgation-bar>
    <div class="shopping-content">
      <div class="content-list">
        <div v-for="(item,index) in shoppingDatas" :key="index" class="content-list-item">
          <!-- check -->
          <img
            :src="checkImg(item.isCheck)"
            @click="onGoodsCheckClick(item)"
            class="item-check"
            alt
          />
          <!-- 图片 -->
          <img :src="item.img" class="item-img" alt />
          <!-- 描述 -->
          <div class="item-desc">
            <!-- 名称 -->
            <p class="item-desc-name">
              <!-- 直营标记 -->
              <direct v-if="item.isDirect"></direct>
              {{item.name}}
            </p>
            <div class="item-desc-data">
              <!-- 价格 -->
              <p class="item-desc-data-price">￥{{item.price|priceValue}}</p>
              <!-- 商品数量的控制组件 -->
              <number-manager
                :defaultNumber="item.number"
                @onChangeNumber="onNumberChange(arguments,item,index)"
              ></number-manager>
            </div>
          </div>
        </div>
      </div>
      <!-- 统计 -->
      <div class="content-total">
        <!-- 全选 -->
        <div class="content-total-check">
          <img @click="onAllCheckClick" :src="checkImg(isAll)" alt />
          <p>全选</p>
        </div>
        <!-- 总价格 -->
        <div class="content-total-price">
          <p class="content-total-price-total">
            合计 :
            <span>￥{{totalData.totalPrice|priceValue}}</span>
          </p>
          <p class="content-total-price-all">
            总额 :
            <span>￥{{totalData.totalPrice|priceValue}}</span>
            &nbsp;&nbsp;
            立减
            <span>￥0.00</span>
          </p>
        </div>
        <!-- 结算 -->
        <div class="content-total-commit">去结算{{totalData.totalNumber}}</div>
      </div>
    </div>
  </div>
</template>
<script>
import NavgationBar from './currency/NavgationBar.vue'
import NumberManager from './goods/NumberManager.vue'
import Direct from './goods/Direct.vue'
export default {
  components: {
    NavgationBar,
    Direct,
    NumberManager
  },
  data() {
    return {
      // src="../assets/img/no-check.svg"
      // 购物车的商品
      shoppingDatas: this.$store.state.shoppingDatas,
      // 总计
      // 是否全选
      isAll: false,
      totalData: {
        // 总价格
        totalPrice: 0,
        // 总数量
        goodsNumber: 0
      }
    }
  },
  created() {
    this.computedGoodsTotal()
  },
  methods: {
    onNumberChange($arguments, item, index) {
      let number = $arguments[0]
      this.$store.commit('changeSoppingDataNumber', {
        index: index,
        number: number
      })
      if (item.isCheck) {
        this.computedGoodsTotal()
      }
    },
    checkImg(isCheck) {
      return isCheck ? require('../assets/img/check.svg') : require('../assets/img/no-check.svg')
    },
    // 全选点击事件
    onAllCheckClick() {
      this.isAll = !this.isAll
      console.log(this.isAll)

      this.shoppingDatas.forEach(item => {
        item.isCheck = this.isAll
      })
      this.computedGoodsTotal()
    },
    // 商品点击事件
    onGoodsCheckClick(item) {
      item.isCheck = !item.isCheck
      this.computedGoodsTotal()
    },
    computedGoodsTotal() {
      this.totalData = {
        // 总价格
        totalPrice: 0,
        // 总数量
        goodsNumber: 0
      }
      this.shoppingDatas.forEach(item => {
        if (item.isCheck) {
          this.totalData.totalPrice += parseFloat(item.price) * parseInt(item.number)
          this.totalData.goodsNumber += parseInt(item.number)
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.shopping {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-flow: column;
  padding: 76/20rem 0;
  .shopping-content {
    background-color: #eee;
    height: 100%;
    display: flex;
    flex-direction: column;
    // 商品列表
    .content-list {
      height: 100%;
      overflow: hidden;
      overflow-y: auto;
      .content-list-item {
        background-color: #fff;
        padding: 0.3rem;
        display: flex;
        border-bottom: 1px solid #ccc;
        .item-check {
          width: 2rem;
          height: 2rem;
          align-self: center;
          padding: 0.3rem;
        }
        .item-img {
          width: 8rem;
          height: 8rem;
        }
        .item-desc {
          width: 100%;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          padding: 0 0.3rem;
          .item-desc-name {
            font-size: 1.2rem;
            line-height: 1.8rem;
          }
          .item-desc-data {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-top: 0.3rem;
            .item-desc-data-price {
              font-size: 1.2rem;
              font-weight: 500;
              color: #f10215;
            }
          }
        }
      }
    }
    // 总计
    .content-total {
      height: 90/20rem;
      display: flex;
      align-items: center;
      background-color: #fff;
      border-top: 1px solid #ccc;
      .content-total-check {
        display: flex;
        align-items: center;
        margin: 0 0.3rem;
        img {
          width: 2rem;
          height: 2rem;
          padding: 0.3rem;
        }
      }
      .content-total-price {
        flex-grow: 2;
        display: flex;
        flex-direction: column;
        .content-total-price-total {
          font-size: 1.2rem;
          margin-bottom: 0.3rem;
          span {
            font-weight: 500;
          }
        }
        .content-total-price-all {
          span {
            font-weight: 500;
          }
        }
      }
      .content-total-commit {
        width: 9rem;
        height: 100%;
        font-size: 1.2rem;
        background-color: #f10215;
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}
</style>
